body {
    background-color: rgb(230, 230, 230);
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.container-fluid-calculadora {
    margin-top: 60px;
}

.col {
    /*Espacio entre columnas */
    text-align: center;
    font-size: 20px;
    margin-left: 3px;
    margin-bottom: 3px;
    padding-top: 30px;
    padding-bottom: 30px;
    background-color: rgb(240, 240, 240);
}

.col:hover {
    /* Cuando el puntero esta sobre el boton */
    background-color: rgb(216, 216, 216);
}

.col:active {
    /* Cuando el boton es activado */
    background-color: rgb(192, 192, 192);
}

/* Columna 1 y 2 */
.funct-1,
.funct-2 {
    text-align: center;
    font-size: 13px;
    padding-top: 15px;
    padding-bottom: 15px;
    margin-left: 3px;
    margin-bottom: 3px;
    font-weight: 500;
    cursor: default;
}

.funct-1:hover,
.funct-2:hover {
    background-color: rgb(216, 216, 216);
}

.funct-1:active,
.funct-2:active {
    background-color: rgb(192, 192, 192);
}

.numeros {
    background-color: white;
    font-weight: 500;
    font-size: 30px;
    padding-top: 23px;
    padding-bottom: 15px;
}

.resultado1,
.resultado2 {
    text-align: right;
}

.resultado1>h1 {
    font-size: 70px;
}

.resultado2>p {
    font-size: 18px;
    margin-bottom: -4px;
}

.cabesal>div>h3 {
    font-weight: 400;
    font-size: 25px;
    margin: 5px;
}

/* Cabesal Historia y Memoria */
.cabesal>div>h4 {
    font-weight: 500;
    font-size: 18px;
    margin: 5px;
}

#div-historial,
#div-memoria {
    text-align: right;
}

#his {
    border-bottom: 3px solid red;
    text-align: center;
}

/* UI */

@media (max-width:  900px) {
    .numeros {
        background-color: red;
        font-weight: 500;
        font-size: 30px;
        padding-top: 23px;
        padding-bottom: 15px;
    }
}